import { Component, ViewContainerRef } from '@angular/core';
import { ToastsManager } from 'ng2-toastr/ng2-toastr';
import { ProcessService } from './process.service';
import { Folder } from './folder';

declare var cheet: any;
declare var $: any;

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  $folder: any;
  breadcrumb: string[];
  $isRunning: boolean;

  constructor(private _toastr: ToastsManager, private _vcr: ViewContainerRef, private _processService: ProcessService){
    this.$folder;
    this.$isRunning = false;
    this.breadcrumb = [];
    this.breadcrumb.push('root');
    this._toastr.setRootViewContainerRef(_vcr);
    cheet('        b a', () => {
      document.getElementById('pbjt').style.display = 'inline';
      this._toastr.info('Konami code! Logro desbloqueado');
    });

    window.onbeforeunload = () => {
      return this._processService.stopServices().subscribe(res=> console.log('Finish Him!'));
    }
  }

  folderChange(folder) {
    if(this.$folder && this.$folder.name !== folder.name) this.breadcrumb.push(folder.name);
    this.$folder = folder;
    this.generateGroupRadios();
  }

  generateGroupRadios() {
    const arr = [];

    this.$folder.content.forEach((rest) => arr.push(rest.path));

    arr.forEach((path) => {
      const arrayIndexs = [];
      this.$folder.content.forEach((rest, index) => {
        if (rest.path === path) {
          arrayIndexs.push(index);
        }
      });
      if (arrayIndexs.length > 1) {
        const key = arrayIndexs.join('');
        this.$folder.content.forEach((rest, index) => {
          if (arrayIndexs.indexOf(index) !== -1) {
            rest.groupRadio = key;
            rest.isSelected = false;
          }
        });
      }
    });
  }
}



